import {
  ListItemText,
  Divider,
  ListItemIcon,
  ListItemButton,
} from "@mui/material";
import PhoneIcon from "@mui/icons-material/Phone";
import AlternateEmailIcon from "@mui/icons-material/AlternateEmail";
import "./workerListItem.scss";
import { useNavigate } from "react-router-dom";
import { useCallback } from "react";

interface WorkerListItemProps {
  name: string;
  tel: string;
  email: string;
  id: string;
}

export const WorkerListItem = ({
  name,
  email,
  tel,
  id,
}: WorkerListItemProps) => {
  const navigate = useNavigate();
  const clickHandler = useCallback(
    () => navigate(`/presenceofworker?id=${id}`),
    [id, navigate]
  );
  return (
    <ListItemButton divider onClick={clickHandler}>
      <ListItemText primary={name} className="worker-list-item-name" />
      <Divider orientation="vertical" flexItem />
      <ListItemIcon sx={{ marginLeft: "30px" }}>
        <PhoneIcon />
      </ListItemIcon>
      <ListItemText primary={tel} className="worker-list-item-contact-info" />
      <Divider orientation="vertical" flexItem />
      <ListItemIcon sx={{ marginLeft: "30px" }}>
        <AlternateEmailIcon />
      </ListItemIcon>
      <ListItemText primary={email} className="worker-list-item-contact-info" />
    </ListItemButton>
  );
};
