import { useLazyQuery, useQuery } from "@apollo/client";
import { Backdrop } from "@mui/material";
import { useCallback, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { PRESENCE_OF_WORKER, WORKER_NAME } from "../../queries/queries";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeList } from "react-window";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { IconButton } from "@mui/material";

import "./presenceOfWorkerView.scss";
import { PresenceOfWorkerListItem } from "../presenceOfWorkerListItem/presenceOfWorkerListItem";

const PresenceOfWorkerView = () => {
  const [serchParams] = useSearchParams();

  const [loadWorkingDays, { loading, data }] = useLazyQuery(
    PRESENCE_OF_WORKER,
    {
      variables: { id: serchParams.get("id") || "" },
    }
  );

  const { data: workerData, loading: workerLoading } = useQuery(WORKER_NAME, {
    variables: { id: serchParams.get("id") || "" },
  });

  useEffect(() => {
    const id = serchParams.get("id");
    if (id) {
      loadWorkingDays();
    }
  }, [serchParams, loadWorkingDays]);

  const navigate = useNavigate();
  const clickBackHandler = useCallback(() => {
    navigate(-1);
  }, [navigate]);

  return (
    <div className="presence-of-worker-view-container">
      <div className="presence-of-worker-view-toolbar">
        <IconButton onClick={clickBackHandler}>
          <ArrowBackIcon />
        </IconButton>
        <span className="presence-of-worker-view-toolbar-header">
          {workerData?.worker?.name}
        </span>
      </div>
      <PresenceOfWorkerListItem
        index={0}
        data={{
          presenceOfWorker: [{ epochDay: 0, hours: 0, constructionName: "" }],
        }}
        style={{ height: "50px" }}
      />
      <div
        style={{ overflow: loading && workerLoading ? "hidden" : "scroll" }}
        className="presence-of-worker-view-list-items-wrapper"
      >
        <AutoSizer>
          {({ height, width }) => {
            return (
              <FixedSizeList
                height={height - 150}
                width={width}
                itemSize={50}
                itemCount={data?.presenceOfWorker?.length || 0}
                itemData={data}
              >
                {PresenceOfWorkerListItem}
              </FixedSizeList>
            );
          }}
        </AutoSizer>
        <Backdrop className="presence-of-worker-view-backdrop" open={loading}>
          <span className="loader"></span>
        </Backdrop>
      </div>
    </div>
  );
};

export default PresenceOfWorkerView;
