import { Divider, ListItem, ListItemIcon, ListItemText } from "@mui/material";
import { useTranslation } from "react-i18next";
import ImageIcon from "@mui/icons-material/Image";
import "./workingDayListItem.scss";
import { ListChildComponentProps } from "react-window";
import { ConstructionWorkingDaysQuery } from "../../__generated__/graphql";

export const WorkingDayListItem = ({
  data,
  style,
  index,
}: ListChildComponentProps) => {
  const { i18n } = useTranslation();
  const typedData = data as ConstructionWorkingDaysQuery | undefined;
  const epochDays = typedData?.workingDays?.[index]?.epochDay;
  const numberOfPhotos = typedData?.workingDays?.[
    index
  ]?.descriptions?.flatMap((description) => description?.photoFileNames).length;

  const description =
    typedData?.workingDays?.[index]?.descriptions?.[0]?.content;

  const dateString = new Date(
    epochDays * 24 * 60 * 60 * 1000
  ).toLocaleDateString(i18n.language, {
    day: "numeric",
    month: "numeric",
    year: "numeric",
    weekday: "short",
  });

  return (
    <ListItem divider style={style} key={index}>
      <ListItemText
        primary={dateString}
        className="working-day-list-item-date"
      />
      <Divider orientation="vertical" flexItem />
      <ListItemIcon className="working-day-list-item-icon">
        <ImageIcon />
        <div className="working-day-list-item-number-container">
          {numberOfPhotos}x
        </div>
      </ListItemIcon>
      <ListItemText
        className="working-day-list-item-description"
        secondary={description}
      />
    </ListItem>
  );
};
