import { useLazyQuery } from "@apollo/client";
import { Backdrop } from "@mui/material";
import { useCallback, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, useSearchParams } from "react-router-dom";
import { CONSTRUCTION_WORKING_DAYS } from "../../queries/queries";
import { WorkingDayListItem } from "../workingDayListItem/workingDayListItem";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeList } from "react-window";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { IconButton } from "@mui/material";

import "./workingDaysView.scss";

const WorkingDaysView = () => {
  const { t } = useTranslation();
  const [serchParams] = useSearchParams();

  const [loadWorkingDays, { loading, data }] = useLazyQuery(
    CONSTRUCTION_WORKING_DAYS,
    {
      variables: { id: serchParams.get("id") || "" },
    }
  );

  useEffect(() => {
    const id = serchParams.get("id");
    if (id) {
      loadWorkingDays();
    }
  }, [serchParams, loadWorkingDays]);

  const navigate = useNavigate();
  const clickBackHandler = useCallback(() => {
    navigate(-1);
  }, [navigate]);

  return (
    <div className="working-days-view-container">
      <div className="working-days-view-toolbar">
        <IconButton onClick={clickBackHandler}>
          <ArrowBackIcon />
        </IconButton>
        <span className="working-days-view-toolbar-header">
          {t("daily records")}
        </span>
      </div>
      <div
        style={{ overflow: loading ? "hidden" : "scroll" }}
        className="working-days-view-list-items-wrapper"
      >
        <AutoSizer>
          {({ height, width }) => {
            return (
              <FixedSizeList
                height={height - 100}
                width={width}
                itemSize={50}
                itemCount={data?.workingDays?.length || 0}
                itemData={data}
              >
                {WorkingDayListItem}
              </FixedSizeList>
            );
          }}
        </AutoSizer>
        <Backdrop className="working-days-view-backdrop" open={loading}>
          <span className="loader"></span>
        </Backdrop>
      </div>
    </div>
  );
};

export default WorkingDaysView;
