import { gql } from "../__generated__/gql";

export const ALL_CONSTRUCTIONS = gql(`
  query allConstructions {
    constructions {
      _id
      name
      locality {
        address
      }
      isActive
      investor {
        name
      }
    }
  }
`);

export const ALL_WORKERS = gql(`
query allWorkers{
  workers{
    name,
    phoneNumber,
    email
    _id
  }
}
`);

export const ALL_MACHINES = gql(`
query allMachines {
  machines {
    name
    unitOfMeasure
    _id
  }
}
`);

export const CONSTRUCTION_WORKING_DAYS = gql(`
query constructionWorkingDays($id: String!){
  workingDays(query: {_constructionId_in: [$id]} limit: 10000) {
    epochDay
    descriptions {
      content
      photoFileNames
    }
  }
}
`);

export const PRESENCE_OF_WORKER = gql(`
query presenceOfWorker($id: String!){
  presenceOfWorker(input: $id) {
    constructionName
    hours
    epochDay
  }
}
`);

export const WORKER_NAME = gql(`
query workerName($id: String!){
  worker(query: {_id: $id}) {
    name
  }
}
`);

export const USAGE_OF_MACHINE = gql(`
query usageOfMachine($id: String!){
  usageOfMachine(input: $id) {
    amount
    constructionName
    epochDay
  }
}
`);

export const MACHINE_NAME = gql(`
query machineName($id: String!){
  machine(query: {_id: $id}) {
    name
    unitOfMeasure
  }
}
`);

