import { ListItemText, Divider, ListItemIcon, ListItemButton } from "@mui/material";
import SquareFootIcon from "@mui/icons-material/SquareFoot";
import "./machineListItem.scss";
import {useCallback} from "react";
import {useNavigate} from "react-router-dom";

interface MachineListItemProps {
  name: string;
  unitOfMeasure: string;
  id: string;
}

export const MachineListItem = ({
  name,
  unitOfMeasure,
  id
}: MachineListItemProps) => {
  const navigate = useNavigate();
  const clickHandler = useCallback(
    () => navigate(`/usageOfMachine?id=${id}`),
    [id, navigate]
  );
  return (
    <ListItemButton onClick={clickHandler} divider>
      <ListItemText primary={name} className="machines-list-item-name" />
      <Divider orientation="vertical" flexItem />
      <ListItemIcon sx={{ marginLeft: "30px" }}>
        <SquareFootIcon />
      </ListItemIcon>
      <ListItemText
        primary={unitOfMeasure}
        className="machines-list-item-unit-of-measure"
      />
    </ListItemButton>
  );
};
