import { Divider, ListItem, ListItemText } from "@mui/material";
import { useTranslation } from "react-i18next";
import "./usageOfMachineListItem.scss";
import { ListChildComponentProps } from "react-window";
import { UsageOfMachineQuery } from "../../__generated__/graphql";

type UsageOfMachineListItemProps = ListChildComponentProps & {unitOfMeasure?: string}
export const UsageOfMachineListItem = ({
  data,
  style,
  index,
  unitOfMeasure
}: UsageOfMachineListItemProps) => {
  const { t, i18n } = useTranslation();
  const typedData = data as UsageOfMachineQuery | undefined;

  //if epochDays === 0 it means that this item is header of list
  const epochDays = typedData?.usageOfMachine?.[index]?.epochDay;
  const isHeaderOfList = epochDays === 0;

  const amount = isHeaderOfList
    ? t("usage") + ` [${unitOfMeasure}]`
    : typedData?.usageOfMachine?.[index]?.amount;

  const construction = isHeaderOfList
    ? t("construction name")
    : typedData?.usageOfMachine?.[index]?.constructionName;

  const dateString = isHeaderOfList
    ? t("date")
    : new Date(epochDays * 24 * 60 * 60 * 1000).toLocaleDateString(
        i18n.language,
        {
          day: "numeric",
          month: "numeric",
          year: "numeric",
          weekday: "short",
        }
      );

  if (isHeaderOfList) {
    style.boxShadow =
      "rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px";
  }

  return (
    <ListItem divider style={style} key={index}>
      <ListItemText
        primary={dateString}
        className={`usage-of-machine-list-item-date ${
          isHeaderOfList && "header"
        }`}
      />
      <Divider orientation="vertical" flexItem />
      <ListItemText
        className={`usage-of-machine-list-item-description ${
          isHeaderOfList && "header"
        }`}
        secondary={construction}
      />
      <Divider orientation="vertical" flexItem />
      <ListItemText
        className={`usage-of-machine-list-item-number-container ${
          isHeaderOfList && "header"
        }`}
        secondary={amount}
      />
    </ListItem>
  );
};
