import * as types from './graphql';
import { TypedDocumentNode as DocumentNode } from '@graphql-typed-document-node/core';

/**
 * Map of all GraphQL operations in the project.
 *
 * This map has several performance disadvantages:
 * 1. It is not tree-shakeable, so it will include all operations in the project.
 * 2. It is not minifiable, so the string of a GraphQL query will be multiple times inside the bundle.
 * 3. It does not support dead code elimination, so it will add unused operations.
 *
 * Therefore it is highly recommended to use the babel-plugin for production.
 */
const documents = {
    "\n  query allConstructions {\n    constructions {\n      _id\n      name\n      locality {\n        address\n      }\n      isActive\n      investor {\n        name\n      }\n    }\n  }\n": types.AllConstructionsDocument,
    "\nquery allWorkers{\n  workers{\n    name,\n    phoneNumber,\n    email\n    _id\n  }\n}\n": types.AllWorkersDocument,
    "\nquery allMachines {\n  machines {\n    name\n    unitOfMeasure\n    _id\n  }\n}\n": types.AllMachinesDocument,
    "\nquery constructionWorkingDays($id: String!){\n  workingDays(query: {_constructionId_in: [$id]} limit: 10000) {\n    epochDay\n    descriptions {\n      content\n      photoFileNames\n    }\n  }\n}\n": types.ConstructionWorkingDaysDocument,
    "\nquery presenceOfWorker($id: String!){\n  presenceOfWorker(input: $id) {\n    constructionName\n    hours\n    epochDay\n  }\n}\n": types.PresenceOfWorkerDocument,
    "\nquery workerName($id: String!){\n  worker(query: {_id: $id}) {\n    name\n  }\n}\n": types.WorkerNameDocument,
    "\nquery usageOfMachine($id: String!){\n  usageOfMachine(input: $id) {\n    amount\n    constructionName\n    epochDay\n  }\n}\n": types.UsageOfMachineDocument,
    "\nquery machineName($id: String!){\n  machine(query: {_id: $id}) {\n    name\n    unitOfMeasure\n  }\n}\n": types.MachineNameDocument,
};

/**
 * The gql function is used to parse GraphQL queries into a document that can be used by GraphQL clients.
 */
export function gql(source: "\n  query allConstructions {\n    constructions {\n      _id\n      name\n      locality {\n        address\n      }\n      isActive\n      investor {\n        name\n      }\n    }\n  }\n"): (typeof documents)["\n  query allConstructions {\n    constructions {\n      _id\n      name\n      locality {\n        address\n      }\n      isActive\n      investor {\n        name\n      }\n    }\n  }\n"];
