import {
  ListItemText,
  Divider,
  ListItemIcon,
  ListItemButton,
  Tooltip,
} from "@mui/material";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import PersonIcon from "@mui/icons-material/Person";
import "./constructionListItem.scss";
import { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";

interface ConstructionListItemProps {
  name: string;
  investorName: string;
  address: string;
  isActive: boolean;
  id: string;
}

export const ConstructionListItem = ({
  name,
  investorName,
  address,
  isActive,
  id,
}: ConstructionListItemProps) => {
  const navigate = useNavigate();
  const clickHandler = useCallback(
    () => navigate(`/workingdays?id=${id}`),
    [id, navigate]
  );
  const { t } = useTranslation();
  return (
    <ListItemButton divider onClick={clickHandler}>
      <ListItemText primary={name} className="construction-list-item-name" />
      <Divider orientation="vertical" flexItem />
      <ListItemIcon sx={{ marginLeft: "30px" }}>
        <LocationOnIcon />
      </ListItemIcon>
      <Tooltip title={address} arrow placement="top-start">
        <ListItemText
          className="construction-list-item-address"
          primary={address}
        />
      </Tooltip>
      <Divider orientation="vertical" flexItem />
      <ListItemIcon sx={{ marginLeft: "30px" }}>
        <PersonIcon />
      </ListItemIcon>
      <ListItemText
        primary={investorName}
        className="construction-list-item-investor"
      />
      <Divider orientation="vertical" flexItem />
      <ListItemText
        className="construction-list-item-state"
        primary={isActive ? t("ongoing") : t("ended")}
      />
    </ListItemButton>
  );
};
