import { Divider, ListItem, ListItemText } from "@mui/material";
import { useTranslation } from "react-i18next";
import "./presenceOfWorkerListItem.scss";
import { ListChildComponentProps } from "react-window";
import { PresenceOfWorkerQuery } from "../../__generated__/graphql";

export const PresenceOfWorkerListItem = ({
  data,
  style,
  index,
}: ListChildComponentProps) => {
  const { t, i18n } = useTranslation();
  const typedData = data as PresenceOfWorkerQuery | undefined;

  //if epochDays === 0 it means that this item is header of list
  const epochDays = typedData?.presenceOfWorker?.[index]?.epochDay;
  const isHeaderOfList = epochDays === 0;

  const hours = isHeaderOfList
    ? t("hours")
    : typedData?.presenceOfWorker?.[index]?.hours;
  const construction = isHeaderOfList
    ? t("construction name")
    : typedData?.presenceOfWorker?.[index]?.constructionName;

  const dateString = isHeaderOfList
    ? t("date")
    : new Date(epochDays * 24 * 60 * 60 * 1000).toLocaleDateString(
        i18n.language,
        {
          day: "numeric",
          month: "numeric",
          year: "numeric",
          weekday: "short",
        }
      );

  if (isHeaderOfList) {
    style.boxShadow =
      "rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px";
  }

  return (
    <ListItem divider style={style} key={index}>
      <ListItemText
        primary={dateString}
        className={`presence-of-worker-list-item-date ${
          isHeaderOfList && "header"
        }`}
      />
      <Divider orientation="vertical" flexItem />
      <ListItemText
        className={`presence-of-worker-list-item-description ${
          isHeaderOfList && "header"
        }`}
        secondary={construction}
      />
      <Divider orientation="vertical" flexItem />
      <ListItemText
        className={`presence-of-worker-list-item-number-container ${
          isHeaderOfList && "header"
        }`}
        secondary={hours}
      />
    </ListItem>
  );
};
