import { useLazyQuery, useQuery } from "@apollo/client";
import { Backdrop } from "@mui/material";
import { useCallback, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { MACHINE_NAME, USAGE_OF_MACHINE } from "../../queries/queries";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeList } from "react-window";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { IconButton } from "@mui/material";

import "./usageOfMachineView.scss";
import { UsageOfMachineListItem } from "../usageOfMachineListItem/usageOfMachineListItem";

const UsageOfMachineView = () => {
  const [serchParams] = useSearchParams();

  const [loadWorkingDays, { loading, data }] = useLazyQuery(USAGE_OF_MACHINE, {
    variables: { id: serchParams.get("id") || "" },
  });

  const { data: machineData, loading: machineLoading } = useQuery(
    MACHINE_NAME,
    {
      variables: { id: serchParams.get("id") || "" },
    }
  );

  useEffect(() => {
    const id = serchParams.get("id");
    if (id) {
      loadWorkingDays();
    }
  }, [serchParams, loadWorkingDays]);

  const navigate = useNavigate();
  const clickBackHandler = useCallback(() => {
    navigate(-1);
  }, [navigate]);

  return (
    <div className="usage-of-machine-view-container">
      <div className="usage-of-machine-view-toolbar">
        <IconButton onClick={clickBackHandler}>
          <ArrowBackIcon />
        </IconButton>
        <span className="usage-of-machine-view-toolbar-header">
          {machineData?.machine?.name}
        </span>
      </div>
      <UsageOfMachineListItem
        index={0}
        unitOfMeasure={machineData?.machine?.unitOfMeasure}
        data={{
          usageOfMachine: [{ epochDay: 0, hours: 0, constructionName: "" }],
        }}
        style={{ height: "50px" }}
      />
      <div
        style={{ overflow: loading && machineLoading ? "hidden" : "scroll" }}
        className="usage-of-machine-view-list-items-wrapper"
      >
        <AutoSizer>
          {({ height, width }) => {
            return (
              <FixedSizeList
                height={height - 150}
                width={width}
                itemSize={50}
                itemCount={data?.usageOfMachine?.length || 0}
                itemData={data}
              >
                {UsageOfMachineListItem}
              </FixedSizeList>
            );
          }}
        </AutoSizer>
        <Backdrop className="usage-of-machine-view-backdrop" open={loading}>
          <span className="loader"></span>
        </Backdrop>
      </div>
    </div>
  );
};

export default UsageOfMachineView;
